@import url('https://fonts.googleapis.com/css2?family=Barlow&family=Merriweather&family=Roboto:wght@300&family=Rubik&display=swap');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Barlow',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
